header{
    height: 111px;
}
a{
    text-decoration: none;
    color: black;
}
a:hover{
    text-decoration-line: underline; 
}
.searchBox{
    display:inline-block;
    width: 450px;
    height:45px;
    border:1px solid black;
    vertical-align: -35px;
    input {
        width:85%;
        height:95%;
        border:none;
        padding-left: 5%;
    }
    button{
        background-color: rgba(0,0,0,0);
        border: none;
        font-size: 20px;
    }
    div{
        margin-top: 10px
    }
    .searchIcon{
            background: url('../../../public/ico.png') no-repeat 0 0;
            width: 24px;
            height: 24px;
            position: relative;
            top: -6px;
            left: 5px;
            vertical-align: sub;
    }
}
.recommend{
    margin-right: 5px;
    margin-left: 10px;
    font-size: 12px;
    color: black;
    text-decoration: none;
}
